img{
    height: 150px;

    width: 150px;
}

div.card {
    border-radius: 24px;
    background-color: rgb(118, 255, 207);
    height: 350px;
    width: 200px;
    margin: 16px;
    padding: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transition-duration: 1s;

}

div.card:hover{
    background-color: rgb(103, 204, 169);
    transform: scale(1.05,1.05);
    box-shadow:  5px 4px 8px rgba(0, 0, 0, 0.275);
}